#main-footer {
	background: url("http://devel.elevatetheusa.org/wp-content/uploads/2017/09/footer_bg-e1508538789437.png");
background-position: top right;
    background-size: cover;
		.grid {
			padding: 10%;
			display: flex;
			height: 55vh;
			@include bp(tablet){
				height: 30vh;
			}
display: flex;
flex-flow: row wrap;
justify-content: space-between;
align-items: center;
			position: relative;
			.col-1-2
			{
				width: 60%;
				color: white;
					@include bp(tablet){
						width: 45%;
					}

				position: absolute;
				left: 12%;
				bottom: 10%;
				.contact{
					padding:4% 0;
					p{
					font-weight: 600;
}
					@include bp(tablet){
						padding: 2% 0;
					}
				}
				.terms{
					font-size: 14px;
					font-weight: bold;
					@include bp(phone-tablet){
						font-size: 12px;
					}
				}

			}
			.col-1-3{
				width: 15%;
				position: absolute;
				right: 5%;
				bottom: 7%;
					@include bp(tablet){
						right: 10%;
					}
			}
		}

		@include bp(phone){
		background: $orange;
		.grid{
			padding: 0;
			height: auto;

			.col-1-2 {
				width: 100%;
				background-color: $orange;
				padding: 10%;
				position: static;
				text-align: center;


			}
			.col-1-3{
				width: 100%;
					padding: 10%;
					background-color: $navy;
				position: static;
				margin: 0 auto;
				text-align: center;
				img{
					width: 50%;
					margin: 0 auto;
				}

			}
		}

		}
}
