.et_vertical_fixed.admin-bar #page-container #main-header {
    top: 0;
	}
#main-header{
	background-color: $navy!important;
.et_mobile_menu{
	border-top:none;
	background-color: $navy!important;
.social_icons{
	background-color: transparent;
	position: relative;
	width: 3em;
	padding-left: 0;
	    margin: 1.2em auto;
			text-align: center;

	a{
			border-bottom: none;
			float: right;
		img{
			position: absolute;
			left: 50%;
			top: 50%;
			max-height: 1.5em;
			transform: translate(-50%,-50%);

		}
	}
}

.donate{
	width: 100%;
	background-color: $lightgrey;


	a{
		color: $navy!important;
			text-align: center;
			border-bottom: none;
	}
}
li{
	text-transform: uppercase;
	// margin: .2em 0em;
	height: 2.5em;
	font-family: $gotham;
	a{
		text-align: center;
		color: white!important;
		border-bottom: $orange 1px solid;
	}
}
}

}

.mobile_menu_bar:before {
	color:$orange;
}
.mobile_menu_bar{
	padding: 1.5em 0!important;
}
