.inner_second_second{
	.et_pb_row{

	.et_pb_column{
		display: flex;
		@include bp(tablet){
			display: flex;
			width: 50%;
		}

		&:first-child{
			align-items: center;
			// position: relative;
			margin: 0;
			.et_pb_text{
				width: 80%;
				color: $navy;
				// @include vh_center;

			}

			&:after{
				content: "";
				background: $lightgrey;
				position: absolute;
				width: .2em;
				top: 0;
				bottom: 0;
				right: 0;
				height: 100%;
			}
		}
		&:last-child{
			justify-content: center;
			// position: relative;
			.et_pb_text{
				// @include vh_center;
			ol{
			text-align: left;
			color: $navy;
			font-family: $roundedmeduim;
			li{
				@extend .heading5;
			}
			line-height: 3em;
			margin-top: 5%;
		}
	}
		}
	}
	}

}
.seven_values{
	// position: relative;
	.et_pb_column_1_3 h1{
		@extend .smaller_h1;
	}

	.et_pb_column{
		&:first-child{
			position: relative;

			.et_pb_text{
				@include custom_center(0%,30%);

				h1 {
						line-height: 1.2em;
				}

		}

		}
	}
}



.dots-list {
width: 100%;
}
.dots-list ol {
padding-left: 150px;
position: relative;
  counter-reset: li;
margin-bottom: 20px;
 padding-left: 10px;
list-style: none !important;
}
.dots-list ol li {
position: relative;
margin-top: 1.5em;
padding-left: 3em;
padding-top: .2em;
line-height: 2em;
// height: 5em;

}

.dots-list ol li:before {
content: "";
background: $orange;
position: absolute;
width: .1em;
top: 2em;
left: 1em;
height: 100%;
// height: 5em;
}
.dots-list ol li:after {
	content: counter(li, decimal-leading-zero);
counter-increment: li;
height: 2em;
width: 2em;
border: .1em solid $orange;
border-radius: 50%;
color: black;
background: white;
text-align: center;
position: absolute;
left: 0;
top: 0;
}
.dots-list ol li:last-child:before {
content: "";
background: rgba(0, 0, 0, 0);
}





.time_line {
	margin: 0;
	padding: 0!important;
	@include bp(tablet){
		margin: 0 auto;
	}
	.et_pb_column_empty{
		@include bp(tablet){
			display: block;
		}
	}

		.et_pb_column{
				&:nth-child(2){
			width: 5%!important;
			margin: 0;
			padding: 0% 5%;

		}
		&:nth-child(odd){
			width: 45%!important;
			margin: 0;
			@include bp(tablet){
				// float: right;
				// width: 60%!important;
				// text-align: left;
			}

		}
	}
	.TL_text_block{
		@include bp(tablet){
			// float: right;
			// text-align: left!important;
		}
		h1{
			@extend .heading5;
			color: white;
		}
		h2 {
			@extend .heading5;
			color: $orange;
			font-weight: 600;
		}
		p{
			color: white;
		}
	}

	&:last-child{
		.dot::before{
			display: none;
		}
	}
		&:nth-child(even){
	.TL_text_block{
			text-align: left;
			// @extend  .et-waypoint et_pb_animation_left;
		}
	}
	&:nth-child(odd){
		.TL_text_block{
				text-align: right;

			}
	}
}
.et_pb_column.et_pb_column_1_3.et_pb_column_1{
	// position: relative;
	// height: 100%;
}



.dot{
	position: relative;
	height: 100%;
	// &:last-child{
	// 	 &:before{
	// 		 background-color: blue;
	// 	 }
	// }
 &:before{
	content: "";
	background: white;
	position: absolute;
	width: 2px;
	top: 0%;
	bottom: 0;
	left: 50%;
	transform: translate(-50%,0);
}


&:after{
	content: "";
	background: white;
	position: absolute;
	width: 2em;
	height: 2em;
	border-radius: 100%;
	// border: 2px solid #35a6cb;
	top: 0%;
	left: 50%;
		transform: translate(-50%,0);
}
}
