g.State{
	.bg_state {
			fill-opacity: 1;
				transition: .3s ease-out;

	}
	.state_name{
		opacity: 0;
		transition: .3s ease-out;
	}
	g#pin_group{
		transform: translateY(0);
		transition: .3s ease-out;
			transform-origin: 50% 50%;
		// position: relative;
	}


	&:hover {
		g#pin_group{
		// animation: movePin .5s ease-out normal;
		transform: translateY(-5px) scale(1.2);
		transition: .5s ease;
		// transform: scale(1.1);
		transform-origin: 50% 50%;
		// filter: url(#f1);

	}
	.pin{
		// transform: scale(1.1);
		// transform-origin: 50% 50%;
			transition: .3s ease-in;
	}
	.bg_state{
			fill-opacity: 0.3;
				transition: .3s ease-in;
	}
		.state_name {
			// visibility: visible;
			opacity: 1;
			transition: .3s ease-in;
		}
}

